import controller from '../../controller';
export default {
  data: function data() {
    return {
      isConnected: false,
      blockHeight: 0
    };
  },
  created: function created() {
    this.updateStatus();
  },
  beforeDestroy: function beforeDestroy() {},
  computed: {
    styleObject: function styleObject() {
      var color = '#F91263';

      if (this.$data.isConnected) {
        return {
          backgroundColor: color,
          borderColor: color
        };
      } else {
        return {
          borderColor: color
        };
      }
    }
  },
  methods: {
    updateStatus: function updateStatus() {
      var _this = this;

      controller.blockchain().then(function (status) {
        _this.$data.isConnected = true;
        _this.$data.blockHeight = status.getBestHeight();
        setTimeout(function () {
          _this.updateStatus();
        }, 5000);
      }).catch(function (error) {
        _this.$data.isConnected = false;
        console.error('Could not connect to blockchain.', error);
        setTimeout(function () {
          _this.updateStatus();
        }, 30000); // Retry after 30s
      });
    }
  },
  components: {}
};